import {mapState} from 'vuex'
import myajax from "@/api/myajax";
import datas from "@/store/kind/kinddata.js";
import store from '@/store';
export default {
  data(){
    return{
      dataBox:[],
      dataUser:[]
    }
  },
  computed: {
    ...mapState({
      list:state => state.kind.list
    })
  },
  mounted () {
    let hideBtn = document.getElementById("hide");
    let hideBox = document.getElementsByClassName("hide-box")[0];
    hideBox.style.display = "none";
    hideBtn.onclick = function() {
      if (hideBox.style.display == "none") {
        hideBox.style.display = "block";
      } else {
        hideBox.style.display = "none";
      }
    };
    let _this = this;
    console.log(_this);
    let dataTime = document.getElementsByClassName("dataTime")[0];
    let now = new Date();
    let year = now.getFullYear();
    let month = now.getMonth() + 1; //月
    let date = "";
    if (month < 10) date += "0";
    date += month;
    dataTime.innerHTML = year + "-" + date;
    for (let k = 1; k < 13; k++) {
      let list = document.createElement("li");
      // console.log(k)
      let k2 = "";
      if (k < 10) k2 += "0";
      k2 += k;
      //console.log(k2)
      list.innerHTML = year + "-" + k2;
      hideBox.appendChild(list);
      list.onclick = function() {
        let dataList = document.getElementsByClassName('DataBox')[0]
        let dataList0 = document.getElementsByClassName('DataBox')[1]
        hideBox.style.display = "none";
        dataList.style.display = "none";
        dataList0.style.display = "block";
    myajax.axios({
      method: "post",
      url: 'Wl_Inshop_Device_Record_RuiWein/WorkTimeSumTopNoAsync?param.accountId=201&param.translateId=1&param.year='+year+'&param.month='+k,
      data:{},
      responseType: "json",
      success:(data) => {
        _this.dataBox = data.Result.SortList
        _this.dataUser = data.Result
        console.log(_this.dataBox)
        let teamInfo = document.getElementsByClassName('team_info')[0]
        let SumTime = document.getElementsByClassName('SumTime')[0]
        
      if( _this.dataUser.SumTime =="本月工作0小时0分钟"){
        teamInfo.innerHTML = "没得工作"
        SumTime .innerHTML= "没得工作"
      }else{
        teamInfo.innerHTML = _this.dataUser.SumTopNo
        SumTime .innerHTML= _this.dataUser.SumTime
        Console.log( _this.dataUser.SumTopNo)
      }
      }
    })
    let index = k;
    console.log(index);
    dataTime.innerHTML = list.innerHTML;
  }
  }

  var id = this.$route.params.Id;
    this.$store.dispatch("getdetail", id);
    console.log(this.$store)
    
  }
}


