export default {
    methods: {
        exit(){
          localStorage.setItem("isLogin", "0");
          this.$router.push('/user')
        }
    },
    mounted () {
        let cName = document.getElementsByClassName('header_con')[0];
        cName.innerHTML = localStorage.companyName
    }
}


