import myajax from '@/api/myajax.js'

export default{
    getdata(AccountId,cb){
        let now = new Date();
        let month = now.getMonth()+1;
        myajax.axios({
            method:'get',
            url:'Wl_Inshop_Device_Record_RuiWein/ModelCheckWorkRecordAsync?param.accountId=201&param.translateId=1&param.year=2018&param.month='+month,
            responseType:'json',
            data:{},
            success:(data)=> {
                cb(data)
               // console.log(localStorage.companyName+'11111111')
               
           
            }
        })

    }
}


