import axios from 'axios';
import baseUrl from "@/api/config";

export default {
    axios(config){
        var { method,url,responseType,success,data } = config
        axios({
            method,
            url:baseUrl + url,
            responseType
        }).then((response) => {
            success(response.data)
        })
    }
}



