import myajax from '@/api/myajax.js'

export default{
    getdata(AccountId,cb){
        myajax.axios({
            method:'post',
            url:'Wl_Inshop_Device_Record_RuiWein/EarlyAvgTopNoAsync?param.accountId=201&param.translateId=1&param.year=2018&param.month=8',
            responseType:'json',
            data:{},
            success:(data)=> {
                cb(data)
                //console.log(data)
               
           
            }
        })

    }
}


