import {mapState} from 'vuex'
import myajax from "@/api/myajax";
import Vue from 'vue'
import vuePickers from 'vue-pickers'
import moment from 'moment'
import datas from "@/store/user/userdata.js";
import store from '@/store';
export default {
  data(){
    return{
      dateTime: '',
      startDate: new Date(),
      dataBox:[]
    }
  },
  computed: {
    ...mapState({
      userlist:state => state.user.userlist
    })
  },
  mounted () {
    let dataTime = document.getElementsByClassName("dataTime")[0];
    let now = new Date();
    let year = now.getFullYear();
    let month = now.getMonth() + 1; //月
    let day = now.getDate();
    let date = "";
    if (month < 10) date += "0";
    date += month+'-';
    if(day < 10)date += "0";
    date += day;
    dataTime.innerHTML = year + "-" + date;

    var id = this.$route.params.Id;
    this.$store.dispatch("getdetail", id)
    console.log(this.$store)
   
  },
  methods: {
    toShow(){
      this.$refs.picker.open()
      
    },

    handleConfirm (data) {
      let dataTime = document.getElementsByClassName("dataTime")[0];
      let date = moment(data).format('YYYY-MM-DD')
      this.dateTime = date
      dataTime.innerHTML = this.dateTime
      let dataList = document.getElementsByClassName('DataBox')[0]
      let dataList0 = document.getElementsByClassName('DataBox')[1]
      dataList.style.display = "none";
      dataList0.style.display = "block";
      myajax.axios({
        method: "post",
        url: 'Wl_Inshop_Device_Record_RuiWein/WorkStartTopNoAsync?param.accountId=201&param.translateId=1&param.date='+this.dateTime,
        data:{},
        responseType: "json",
        success:(res) => {
          this.dataBox = res.Result.SortList
          console.log(this.dataBox)
          for(let g=1;g<this.dataBox.length;g++){
            let teamInfo = document.getElementsByClassName('team_info')[0]
            let StartTime = document.getElementsByClassName('StartTime')[0]
          if(this.dataBox[g].TimeText == "未打卡"){
            teamInfo.innerHTML = "没得排名"
            StartTime .innerHTML= "没得工作"
          }else{
            teamInfo.innerHTML = _this.dataBox[g].TopNo
            StartTime .innerHTML= _this.dataBox[g].StartTime
          
          }
  
        }
        }
      })

    }
  }
}


