import myajax from '@/api/myajax.js'

export default{
    getdata(AccountId,cb){
        let now = new Date();
        let month = now.getMonth()+1;
        myajax.axios({
            method:'post',
            url:'Wl_Inshop_Device_Record_RuiWein/WorkStartTopNoAsync?param.accountId=201&param.translateId=1&param.date=2018-08-20',
            responseType:'json',
            data:{},
            success:(data)=> {
                cb(data)
                //console.log(localStorage.companyName+'2222222222')
               
           
            }
        })

    }
}


